.App {
    text-align: center;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.searchBarCont {
    width: 100%;
    background-image: linear-gradient(40deg, #f386a6 0%, #ec8c69 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10vh 0;
}

.logo {
    color: #fff;
    font-family: 'Pacifico', cursive;
    font-size: 4rem;
    font-weight: lighter;
}

.header {
    color: #545b66;
    font-family: 'Goudy Bookletter 1911', serif;
    font-weight: lighter;
    font-size: 1.5rem;
    margin-top: 1.5rem;
}

.searchForm {
    margin: 2rem 1rem;
    width: 100%;
}

.searchBar {
    width: 75%;
    max-width: 500px;
    height: 1.75rem;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding: 0 1rem;
    border: none;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: -5px 5px 15px rgba(80, 80, 80, 0.2);
}

.searchButton {
    width: 25%;
    max-width: 100px;
    height: 1.75rem;
    min-width: max-content;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border: none;
    color: rgb(255, 255, 255);
    background-color: #a4b4ca;
    box-shadow: 0px 5px 15px rgba(61, 61, 61, 0.2);
}

.searchButton:active {
    color: rgb(80, 80, 80);
    border: 1px solid rgb(138, 138, 138);
}
.searchButton:hover {
    color: rgb(126, 126, 126);
    background-color: #acb9cc;
    cursor: pointer;
}

.searchBar:focus,
.searchButton {
    outline: none;
}

.recipeContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 10vh 5px;
    max-width: 1300px;
    margin: 0 auto;
}

.heroImageCont {
    width: 100%;
    margin: 0 0 10vh;
}

.heroImage {
    width: 20vw;
    max-width: 200px;
    opacity: 0.75;
    display: block;
    margin: 0 auto;
}

@media screen and (max-width: 800px) {
    .searchBarCont {
        padding: 5vh 10vw;
    }
    .logo {
        font-size: 3rem;
    }
    .recipeContainer {
        padding: 10vh 10vw;
    }
    .heroImage {
        width: 30vw;
    }
}

@media screen and (max-width: 430px) {
    .logo {
        font-size: 2rem;
    }
    .header {
        font-size: 1.25rem;
    }
    .recipeContainer {
        padding: 10vh 5px;
    }
    .heroImage {
        width: 50vw;
    }
}